const galleryList = document.querySelector('.gallery.js-gallery');
const imgs = [];
galleryItems.forEach(item => {
  const el = document.createElement('li');
  el.classList.add('gallery__item');
  const link = document.createElement('a');
  link.classList.add('gallery__link');
  link.href = item.original;
  const img = document.createElement('img');
  img.classList.add('gallery__image');
  img.src = item.preview;
  img.alt = item.description;
  img.dataset.source = item.original;
  link.append(img);
  el.append(link);
  imgs.push(el);
});
galleryList.append(...imgs);

galleryList.addEventListener('click', opn);
function opn(e) {
  if (e.target.nodeName !== 'IMG') return;
  console.log('тык');
  const url = e.target.dataset.source;
  const modal = document.querySelector('.lightbox.js-lightbox');
  modal.classList.add('is-open');
  const imgModal = document.querySelector('.lightbox__image');
  imgModal.src = url;
}

const cls = document.querySelector('button[data-action="close-lightbox"]');
cls.addEventListener('click', clsFunc);
function clsFunc() {
  const modal = document.querySelector('.lightbox.js-lightbox');
  modal.classList.remove('is-open');
  const imgModal = document.querySelector('.lightbox__image');
  imgModal.src = '';
}
